export default {
    props: {
        data: {
            type: Object,
            default: null
        },
        list: {
            type: String,
            default: 'total_storage_list'
        }
    },
    methods: {
        itemName(item){
            return this.list === 'store_storage_list'?item.goods_storage:item;
        }
    }
}



